<template>
  <transition @after-leave="handleAfterLeave" name="fade">
    <div class="toast" v-show="isShow">
      {{ msg }}
    </div>
  </transition>
</template>

<script>
export default {
  /*   props: {
    msg: {
      type: String,
      required: true
    }
  }, */
  data() {
    return {
      isShow: false,
      duration: 3000,
      msg: ''
    }
  },
  methods: {
    handleAfterLeave() {
      this.$destroy(true)
      this.$el.parentNode.removeChild(this.$el)
    },
    showMsg() {
      this.isShow = true
      setTimeout(() => {
        this.isShow = false
      }, this.duration)
    }
  }
}
</script>

<style lang='less' scoped>
.toast {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  padding: 5px 10px;
  border-radius: 4px;
  z-index: 9999999999999999999;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
