export default {
  cartTotal(state) {
    if (Object.keys(state.cartItem).length === 0) return 0
    let total = 0
    for (const key in state.cartItem) {
      total += state.cartItem[key].length
    }
    return total
  },
  isAllSelected(state) {
    if (Object.keys(state.cartItem).length === 0) return false
    for (const key in state.cartItem) {
      const flag = state.cartItem[key].findIndex(item => !item.isActived)
      if (flag !== -1) {
        return false
      }
    }
    return true
  },
  activeCount(state) {
    let count = 0
    for (const key in state.cartItem) {
      state.cartItem[key].forEach(item => {
        if (item.isActived) count++
      })
    }
    return count
  },
  totalPrice(state) {
    let price = 0
    for (const key in state.cartItem) {
      state.cartItem[key].forEach(item => {
        if (item.isActived) price += parseFloat(item.price) * item.quan
      })
    }
    return price
  }
}
