import {
  request
} from './request'
// 获取分类页左侧大类数据
export function getCategory() {
  return request({
    url: '/category'
  })
}
// 获取右侧细分类目数据
export function getSubCategory(maitKey) {
  return request({
    url: '/subcategory',
    params: {
      maitKey
    }
  })
}
// 获取分类推荐商品数据
export function getCategoryDetail(miniWallkey, type) {
  return request({
    url: '/subcategory/detail',
    params: {
      miniWallkey,
      type
    }
  })
}
