import {
  getCategory,
  getSubCategory,
  getCategoryDetail
} from '@/network/category.js'

export default {
  state: () => ({
    category: [],
    subCategory: [],
    cateDetail: [],
    activedCategory: '',
    changed: false
  }),
  mutations: {
    updateCategory(state, payload) {
      state.category = payload
      state.activedCategory = state.category[0].maitKey
    },
    updateSubCategory(state, payload) {
      state.subCategory = payload
    },
    updateCateDetail(state, payload) {
      state.cateDetail = payload
    },
    updateActivedCategory(state, payload) {
      state.activedCategory = payload
    },
    markChanged(state, payload) {
      state.changed = payload
    }
  },
  actions: {
    async getCategoryData({
      commit
    }) {
      const cateRes = await getCategory()
      if (!cateRes.success) return this.$toast('请求分类列表失败')
      commit('updateCategory', cateRes.data.category.list)
      // this.category = cateRes.data.category.list
    },
    async getSubCategoryData({
      commit
    }, products) {
      const subCateRes = await getSubCategory(products)
      if (!subCateRes.success) return this.$toast('请求详情分类列表失败')
      commit('updateSubCategory', subCateRes.data.list)
      // console.log(subCateRes)
      // this.subCategory = subCateRes
    },
    async getCateDetailData({
      commit
    }, products) {
      const cateDetailRes = await getCategoryDetail(products, 'pop')
      commit('updateCateDetail', cateDetailRes)
      // console.log(cateDetailRes)
      // this.cateDetail = cateDetailRes
    },
    changeActivedCategory({
      dispatch,
      commit
    }, products) {
      commit('updateActivedCategory', products.maitKey)
      dispatch('getSubCategoryData', products.maitKey)
      dispatch('getCateDetailData', products.miniWallkey)
      commit('markChanged', true)
    }
  }
}
