export default {
  addGoodsToCart({
    state,
    commit
  }, products) {
    // 添加旗帜 用于标识购物车中是否有同一商店的商品
    let flag = true
    for (const key in state.cartItem) {
      if (key === products.shopId) {
        const target = state.cartItem[key].find((item) => {
          return item.iid === products.iid
        })
        if (target) {
          commit('changeGoodsTotal', {
            item: target
          })
        } else {
          products.quan = 1
          state.cartItem[key].push(products)
        }
        flag = false
      }
    }
    if (flag) {
      state.cartItem = {
        ...state.cartItem,
        [products.shopId]: []
      }
      products.quan = 1
      state.cartItem[products.shopId].push(products)
    }
  },
  toAllSelect({
    getters,
    commit
  }) {
    if (getters.isAllSelected) {
      commit('allDeselect')
    } else {
      commit('allSelect')
    }
  }
}
