import Vue from 'vue'
import Vuex from 'vuex'

import actions from './actions'
import getters from './getters'
import category from './modules/category'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cartItem: {}
  },
  mutations: {
    changeGoodsTotal(state, payload) {
      // 参数为对象形式传入item与num
      // 其中Num为数字不传为+1，传-1为-1传其他正整数使件数改为其他整数
      if (!payload.num) {
        payload.item.quan++
      } else if (payload.num === -1) {
        payload.item.quan--
      } else if (payload.num >= 0 && payload.num.isInteger) {
        payload.item.quan = payload.num
      }
      state.cartItem = JSON.parse(JSON.stringify(state.cartItem))
    },
    changeGoodsActived(state, payload) {
      var target = state.cartItem[payload.shopid].find(item => item.iid === payload.iid)
      target.isActived = !target.isActived
    },
    allSelect(state, payload) {
      for (const key in state.cartItem) {
        state.cartItem[key].forEach(item => {
          item.isActived = true
        })
      }
    },
    allDeselect(state, payload) {
      for (const key in state.cartItem) {
        state.cartItem[key].forEach(item => {
          item.isActived = false
        })
      }
    }
  },
  actions,
  getters,
  modules: {
    category
  }
})
